<template>
  <div class="VideosEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-select v-model="video.category" placeholder="">
          <el-option
            v-for="category in categories"
            :key="category._id"
            :label="category.name"
            :value="category._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input placeholder="" v-model="video.title" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" v-model="video.playSort" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker v-model="video.createdAt" type="date" placeholder=""></el-date-picker>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUploadImg"
          :on-error="errorUpload"
          :headers="addHeaders"
        >
          <img v-if="video.imgUrl" :src="video.imgUrl" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUploadVideo"
          :on-error="errorUpload"
          :headers="addHeaders"
        >
          <!-- <img v-if="video.url" :src="video.url" class="avatar" /> -->
          <video controls v-if="video.url" class="avatar">
            <source :src="video.url" type="video/mp4" />
            <source :src="video.url" type="video/ogg" />Video
          </video>
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "VideosEdit",
  data() {
    return {
      video: {
        _id: "",
        title: "",
        url: "",
        imgUrl: "",
        category: "",
        playSort: 0,
        createdAt: new Date()
      },
      isShowAlert: false,
      alertMsg: "",
      categories: []
    };
  },
  methods: {
    // 
    afterUploadImg(res) {
      this.$set(this.video, "imgUrl", res.imgUrl);
    },
    // 
    afterUploadVideo(res) {
      this.$set(this.video, "url", res.imgUrl);
    },
    // 
    errorUpload(err) {
      // 401token
      if (err.status === 401) {
        this.$message({
          type: "error",
          message: "token"
        });
        this.$router.push("/Login");
      }
    },

    // 
    add() {
      // 
      let { title, url, imgUrl, category } = this.video;
      title = title.trim();
      if (!title) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/videos", { title, url, imgUrl, category })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/videos/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/videos", { _id, title, url, imgUrl, category })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/videos/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getvideoById() {
      let _id = this.$route.params._id;
      let res = await this.$http.get(`/videos?_id=${_id}`);
      this.video = res.data.data;
    },
    // 
    async getVideoCategories() {
      let parent = "5dca69e31cfca528dea00e5a";
      let result = await this.$http.get(`/categories?parent=${parent}`); //
      this.categories = result.data.data;
    }
  },
  created() {
    //created
    this.$route.params._id && this.getvideoById(); //_id
    this.getVideoCategories();
  }
};
</script>
<style lang="scss" scoped>
.avatar-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 378px;
  height: auto;
  display: block;
}
</style>