<template>
  <div class="ArticlesEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model="article.title" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="article.categories" multiple placeholder="">
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <vue-editor v-model="article.content" useCustomImageHandler @image-added="handleImageAdded" id="editor"></vue-editor>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
import { VueEditor } from "vue2-editor";    //
export default {
  name: "ArticlesEdit",
  data() {
    return {
      article: {
        _id: "",
        title: "",
        categories: [],
        content: ""
      },
      isShowAlert: false,
      alertMsg: "",
      categories: []
    };
  },
  components:{
    VueEditor
  },
  methods: {
    // 
    add() {
      // 
      let { title, categories, content } = this.article;
      title = title.trim();
      if (!title) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/articles", { title, categories, content })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/articles/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/articles", { title, categories, content, _id })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/articles/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getarticleById() {
      let _id = this.$route.params._id;
      let res = await this.$http.get(`/articles?_id=${_id}`);
      this.article = res.data.data;
    },
    // 
    async getCategories() {
      let parent = '5dab45ce9265ba7350670065'
      let result = await this.$http.get(`/categories?parent=${parent}`);
      this.categories = result.data.data;
    },

    // 
   async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      var formData = new FormData();
      formData.append("file", file);
      // ajax
      let res=await this.$http.post(this.fileUploadAction,formData)
      // console.log(res.data.imgUrl)
      Editor.insertEmbed(cursorLocation, "image", res.data.imgUrl);
          resetUploader();
  }
  },
  created() {
    //created
    this.getCategories();     //
    this.$route.params._id && this.getarticleById(); //_id
  }
};
</script>
