<template>
  <div class="GoodsEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model="good.name" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="fileUploadAction"
          :show-file-list="false"
          :on-success="afterUpload"
          :on-error="errorUpload"
          :headers="addHeaders"
        >
          <img v-if="good.icon" :src="good.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "GoodsEdit",
  data() {
    return {
      good: {
        _id: "",
        name: "",
        icon: ""
      },
      isShowAlert: false,
      alertMsg: ""
    };
  },
  methods: {
    // 
    afterUpload(res) {
      //res
      this.good.icon = res.imgUrl;
    },
    // 
    errorUpload(err) {
      // 401token
      if (err.status === 401) {
        this.$message({
          type:'error',
          message:'token'
        })
        this.$router.push("/Login");
      }
    },

    // 
    add() {
      // 
      let { name, icon } = this.good;
      name = name.trim();
      if (!name) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/goods", { name, icon })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/goods/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/goods", { _id, name, icon })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/goods/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getgoodById() {
      let _id = this.$route.params._id;
      let res = await this.$http.get(`/goods?_id=${_id}`);
      this.good = res.data.data;
    }
  },
  created() {
    //created
    this.$route.params._id && this.getgoodById(); //_id
  }
};
</script>
<style lang="scss" scoped>
.avatar-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>