<template>
  <div class="AdminUsersEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model="adminUser.username" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input clearable show-password placeholder="" type="password" v-model="adminUser.password" @focus="isShowAlert=false"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "AdminUsersEdit",
  data() {
    return {
      adminUser: {
        _id: "",
        username: "",
        password: ""
      },
      isShowAlert: false,
      alertMsg: ""
    };
  },
  methods: {
    // 
    add() {
      // 
      let { username, password } = this.adminUser;
      username = username.trim();
      if (!username) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/adminUsers", { username, password })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/adminUsers/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/adminUsers", { _id, username, password })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/adminUsers/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getadminUserById() {
      let _id = this.$route.params._id;
      let res = await this.$http.get(`/adminUsers?_id=${_id}`);
      this.adminUser = res.data.data;
    }
  },
  created() {
    //created
    this.$route.params._id && this.getadminUserById(); //_id
  }
};
</script>
