<template>
  <div class="AdvertsEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-input placeholder="" v-model="advert.name" @focus="isShowAlert=false"></el-input>
      </el-form-item>

    <!--  -->
     <el-button size="small" type="primary" icon="el-icon-plus" plain @click.prevent="advert.items.push({img_url:'',link_url:'',title:''})"></el-button>
          <div class="skills-box">
            <div class="item" v-for="(item, index) in advert.items" :key="index">
              <el-form-item label="">
                <el-input placeholder="" v-model="item.title" @focus="isShowAlert=false"></el-input>
              </el-form-item>
              <el-form-item label="">
                 <el-upload
              class="avatar-uploader"
              :action="fileUploadAction"
              :headers="addHeaders"
              :show-file-list="false"
              :on-success="res=>item.img_url=res.imgUrl"
            >
              <img v-if="item.img_url" :src="item.img_url" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
              </el-form-item>
              <el-form-item label="">
                <el-input placeholder="" type="text" v-model="item.link_url" @focus="isShowAlert=false"></el-input>
              </el-form-item>             
             <el-form-item>
                <el-button size="small" type="danger" icon="el-icon-delete" plain @click.prevent="advert.items.splice(index,1)"></el-button>
             </el-form-item>
            </div>
          </div>

      <el-form-item style="margin-top:20px;">
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "AdvertsEdit",
  data() {
    return {
      advert: {
        _id: "",
        name: "",
        items: []
      },
      isShowAlert: false,
      alertMsg: ""
    };
  },
  methods: {
    // 
    add() {
      // 
      let { name, items } = this.advert;
      name = name.trim();
      if (!name) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/adverts", { name, items })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/adverts/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/adverts", { _id, name, items })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/adverts/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getadvertById() {
      let _id = this.$route.params._id;
      let res = await this.$http.get(`/adverts?_id=${_id}`);
      this.advert = res.data.data;
    }
  },
  created() {     //created
   this.$route.params._id && this.getadvertById();     //_id
  }
};
</script>
<style lang="scss" scoped>
.avatar-uploader .el-upload {
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>