// axios
import axios from "axios";

// Vue
import Vue from "vue";

// router
import router from "./router";

// axios
const http = axios.create({
  baseURL:process.env.VUE_APP_API_URL || '/admin/api',      //appnpm run buildVUE_APP_API_URLbaseURL'/admin/api'baseURL   .env.development
  // baseURL: "http://127.0.0.1:3000/admin/api",
  timeout: 1000
});

// ---
router.beforeEach(async (to, from, next) => {
  // 
  if (to.meta.isPublic === true) {
    next();
  } else {
    // token
    let token = localStorage.getItem("token");
    if (!token) {
      return next("/Login");
    }
    // tokentoken
    await http.post("/tokenVerify");
    next();
  }
});

// --
http.interceptors.request.use(
  config => {
    if (config.url === "/login") {
      //
      return config;
    }
    // apitoken
    // token
    let token = localStorage.getItem("token");
    // token
    if (!token) {
      return router.push("/Login");
    }
    // token
    config.headers.Authorization = token;
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

// --
http.interceptors.response.use(
  res => {
    return Promise.resolve(res);
  },
  err => {
    //
    Vue.prototype.$message({
      type: "error",
      message: err.response.data.msg
    });
    // 401
    if (err.response.status === 401) {
      router.push("/Login");
    }
    return Promise.reject(err);
  }
);

// axios
export default http;
