<template>
  <div class="ArticlesList">
    <h1></h1>
    <el-table :data="articles" style="width: 100%" stripe fit>
      <el-table-column prop="_id" label="_id" style="width:220px;"></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="pub_date" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/articles/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="primary" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "ArticlesList",
  data() {
    return {
      articles: []
    };
  },
  methods: {
    async getarticles() {
      let res = await this.$http.get("/articles");
      this.articles = res.data.data;
    },
    del(row) {
      // 
      this.$confirm(`"${row.title}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // ajax
          let res = await this.$http.delete(`/articles?_id=${row._id}`);
          if (res.data.code === 0) {
            //
            this.$message({
              type: "success",
              message: "!"
            });
            // 
            this.getarticles();
          }
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    this.getarticles();
  }
};
</script>
