<template>
  <div class="CategoriesEdit">
    <h1>{{$route.params._id?'':''}}</h1>
    <el-form @submit.native.prevent>
      <el-form-item label="">
        <el-select v-model="category.parent" placeholder="">
          <el-option v-for="item in parents" :key="item._id" :label="item.name" :value="item._id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input placeholder="" v-model="category.name" @focus="isShowAlert=false" @keyup.native.enter="add"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.prevent="add"></el-button>
      </el-form-item>
    </el-form>
    <el-alert :title="alertMsg" type="error" show-icon v-show="isShowAlert"></el-alert>
  </div>
</template>
<script>
export default {
  name: "CategoriesEdit",
  data() {
    return {
      category: {
        _id: "",
        name: ""
      },
      parents: [],
      isShowAlert: false,
      alertMsg: ""
    };
  },
  methods: {
    // 
    add() {
      // 
      let { name, parent } = this.category;
      name = name.trim();
      if (!name) {
        this.alertMsg = "";
        this.isShowAlert = true;
        return;
      }
      // 
      let { _id } = this.$route.params;
      if (!_id) {
        // ---ajax
        this.$http
          .post("/categories", { name, parent })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/categories/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      } else {
        // ---ajax
        this.$http
          .put("/categories", { _id, name,parent })
          .then(res => {
            // 
            this.$message({
              type: "success",
              message: ""
            });
            this.$router.push("/categories/list");
          })
          .catch(err => {
            // 
            this.alertMsg = `${err}`;
            this.isShowAlert = true;
          });
      }
    },
    // _id
    async getCategoryById() {
      // _id
      let _id = this.$route.params._id;
      if (!_id) {
        return;
      }
      let res = await this.$http.get(`/categories?_id=${_id}`);
      this.category = res.data.data;
    },
    // 
    async getParents() {
      let res = await this.$http.get(`/categories`);
      this.parents = res.data.data;
    }
  },
  created() {
    this.getParents();
    this.getCategoryById();
  }
};
</script>
