<template>
  <div class="AdminUsersList">
    <h1></h1>
    <el-table :data="adminUsers" style="width: 100%" stripe fit>
      <el-table-column prop="_id" label="_id" style="width:220px;"></el-table-column>

      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="200">
        <template slot-scope="scope" v-if="scope.row.username !== 'admin'">
          <el-button
            :disabled="scope.row.username === 'admin'"
            type="primary"
            size="small"
            @click="$router.push(`/adminUsers/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="primary" size="small" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "AdminUsersList",
  data() {
    return {
      adminUsers: []
    };
  },
  methods: {
    async getadminUsers() {
      let res = await this.$http.get("/adminUsers");
      this.adminUsers = res.data.data;
    },
    del(row) {
      // 
      this.$confirm(`"${row.username}" `, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          // ajax
          let res = await this.$http.delete(`/adminUsers?_id=${row._id}`);
          if (res.data.code === 0) {
            //
            this.$message({
              type: "success",
              message: "!"
            });
            // 
            this.getadminUsers();
          }
        })
        .catch(() => {
          // 
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  },
  created() {
    this.getadminUsers();
  }
};
</script>
